.hero {
  position: relative;
  height: 100vh;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include small-tablet {
    height: 150vh;
  }
  @include mobile {
    height: 700px;
    width: 100%;
  }
  @include small-mobile {
    height: 80vh;
  }
  &_Header {
    display: inline-flex;
    position: absolute;
    z-index: 2;
    color: #fff;
    margin-bottom: 22%;
    text-align: center;
      font-size: 3.6rem;
      font-family: 'Roboto', sans-serif;
      font-weight: 600;
      padding-top: 15%;
      @include small-tablet {
        margin-bottom: 80%;
      }
      @include mobile {
        font-size: 2.6em;
      }
      @include small-mobile {
        margin-bottom: 100%;
        font-size: 2.2em;
      }
  }
  &_btn {
    display: flex;
    position: absolute;
    z-index: 2;
    color: #fff;
    margin-top: 150px;
    &:not(:last-child) {
      margin-right: 20px;
    }
    @include small-mobile {
      &:not(:last-child) {
        margin-right: 0px;
      }
    }
    &_element{
      z-index: 1;
          width: 300px;
          font-size: 20px;
          color: #fff;
          padding: 15px;
          font-family: 'Roboto', sans-serif;
          border-radius: 20px;
          background-color: #569921;
          background-repeat:no-repeat;
          border: #3b6800 2px solid;
          cursor:pointer;
          overflow: hidden;
          transition: all .2s ease-in-out;
          &:hover{
              background-color: #48811c;
              color: rgba(#fff, .5);
              text-decoration: none;
          }
          &:not(:last-child){
              margin-right: 20px;
              @include small-tablet {
                margin-right: 0;
              }
          }
          @include small-tablet  {
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            margin-bottom: 20px;
          }
          @include mobile {
            max-width: 576px;
            &:not(:first-child){
              margin-top: 40px;
          }
          }
    }
    @include small-tablet  {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      margin-top: 0px;
    }
    @include mobile {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      margin: 0;
      .hero_btn_element{
        margin: 0;
      }

      .hero_btn_element:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    @include small-mobile {
      display: flex;
      margin-top: 20px;
      align-items: center;
      justify-content: center;
    }
  }
  & .image {
    z-index: 1;
    margin: 0;
    padding: 0;
    position: absolute;
    width: 633px;
    height: 600px;
    object-fit: cover;
    bottom: -12px;
    left: 60%;
    opacity: 0.7;
    @include small-tablet {
      left: 5%;
    }
    @include mobile {
      width: 320px;
      height: 290px;
      left: 20%;
      bottom: 0;
    }
    @include small-mobile {
      left: 0;
      bottom: 0;
    }
  }
  &_overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#000, .3);
    }
}

